import { useSelector } from "react-redux";
import CartItem from "./cartItem";
import { getCartItems, getTotalPrice } from "../../redux/cartSlice";
import { Link } from 'react-router-dom';
import cart from '../../images/cart.jpg'
import Swal from 'sweetalert2'
const Cart = () => {
    const alert =() => {
        Swal.fire({
            icon: "error",
            title: "...",
            text: "  ,   ",
          });
    }
    const cartItem = useSelector(getCartItems);
    const totalPrice = useSelector(getTotalPrice);
    
    return(
        <div className="header">
           
            { cartItem.length === 0 ? (<div className="emptyCart">
                <img src={cart} alt="cart"  width="200px"/>
                <span>  </span>
                
                </div>):
            (<div>
               {cartItem.map((cartItem) =>   <CartItem key={cartItem.id} cartItem = { cartItem }/>)}
               <div className="header">
               <h6>  {totalPrice}</h6>
               <button className='link main' onClick={alert}> </button>
               </div>
            </div>)}

            <Link className='link back' to="/dishes">  </Link>
       
        </div>)
}
export default Cart;